<template>
  <div class="rangeContainer">
    <div class="rangeValue">
      {{currentWigth + rangeStart}}<span>%</span>
    </div>
    <input type="range" id="inputRange" :value="rangeStart + currentWigth" :min="minWight" :max="maxWight"
    @input="currentWigth = $event.target.value - rangeStart">
    <div class="rangeButtonContainer">
    <Button :text="rangeText25" @rangeChange="rangeChange(25)"/>
    <Button :text="rangeText50" @rangeChange="rangeChange(50)"/>
    <Button :text="rangeText75" @rangeChange="rangeChange(75)"/>
    <Button :text="rangeText100" @rangeChange="rangeChange(100)"/>
    </div>
  </div>
</template>
<script>
import Button from './Button'
import store from '../store/index'
import { mapState } from 'vuex'

export default {
  components: {Button},
  name: 'Range',
  data() {
    return {
      maxWight: 100,
      minWight: 0,
      currentWigth: 0,
      rangeText25: '25%',
      rangeText50: '50%',
      rangeText75: '75%',
      rangeText100: '100%',
      localCount: 20
    }
  },
    computed: mapState({
    rangeStart: state => state.rangeStart,
  }
    ),
  methods: {
    range25() {
      this.currentWigth = 25
    },
    rangeChange(x) {
      this.currentWigth = x
      store.commit('rangeDelete')
    }
  }
}
</script>
