@import '../style/variables.less';
@import '../style/common.less';
.rangeContainer {
  width: 500px;
  padding: 15px;
  margin: 0 auto;
  @grayColor;
  box-shadow: 10px 5px 5px @grayColor;
  @media @before-w768 {
    width: 300px;
  }
}
.rangeValue {
  text-align: left;
  margin-bottom: 30px;
  font-size: 18px;
  color: @colorPink;
  span {
    margin-left: 5px;
  }
}
#inputRange {
  overflow: hidden;
  width: 100%;
  height: 36px;
  text-align: center;
  -webkit-appearance: none;
  background: linear-gradient(to right, @bgBlockYellow, @colorYellow);
  border-radius: 15px;
}

input[type='range']::-webkit-slider-thumb {
  width: 30px;
  height: 30px;
  -webkit-appearance: none;
  cursor: ew-resize;
  background: @colorPink;
  border-radius: 50%;
}

.rangeButtonContainer {
  display: flex;
  width: 65%;
  margin: 0 auto;
  margin-top: 30px;
  justify-content: space-between;
}
</style>